.page {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.page__body {
  margin: 0;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__item {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: relative;
}

@keyframes arrows {
  from {
    transform-origin: bottom;
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform-origin: bottom;
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

.stopwatch__item:after {
  content: "";
  z-index: -1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: arrows;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stopwatch--speed-up .stopwatch__item:after {
  animation-duration: 10s;
}

.stopwatch__item:before {
  content: "";
  z-index: -2;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: arrows;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stopwatch--speed-up .stopwatch__item:before {
  animation-duration: 600s;
}
